*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  #nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #6b1111;
    color: white;
    padding:0px 10px;
  }
  #menus{
    display: flex;
    align-items: center;
  }
  /*Descendent Selector alternate is Child Selector*/
  #menus p{
    color: white;
    padding:0px 10px;
  }
  /*to remove underline from link component*/
  #menus a{
    text-decoration: none;
  }
  #menus a:hover {
    transform: scale(1.2,1.2)rotate(360deg);
    transition: 600ms;
  }
  #logo 
  {
    display: flex;
    align-items: center;
  }
