#button1{
    position: absolute;
    bottom: 20px;
    right: 60px;
    width: 30px;
    height: 30px;
    border: 3px solid #21a6ad;
    border-radius: 50%;
    cursor: pointer;
}
#button2{
    position: absolute;
    bottom: 20px;
    right: 20px;
    height: 30px;
    width: 30px;
    border: 3px solid #21a6ad;
    border-radius: 50%;
    cursor: pointer;
}
#center-text{
    position: absolute;
    top:50%;
    left: 50%;
    transform: translate(-50%,-50%);
    font-size: 110px;
}
#center-text:hover{
    transform: scale(10,10);
    transition: 600ms;
}